
import vSwiper from "../components/vSwiper"

import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

const list = () => ['详情', '导师', '直播间']

export default {
components: {
vSwiper,

Card,
Swiper,
SwiperItem,
Tab,
TabItem
},
data() {
return {
swiperItemIndex: 1,     // 轮播图数据
swiperHeight: "1.65rem",
lunbotuList: [
{
url: 'javascript:',
img:"http://39.105.41.102:8000/dev/jiayuanshu-frontend/raw/master/src/assets/images/banner01.png",
}, {
url: 'javascript:',
img:"http://39.105.41.102:8000/dev/jiayuanshu-frontend/raw/master/src/assets/images/banner02.png",
}, {
url: 'javascript:',
img:"http://39.105.41.102:8000/dev/jiayuanshu-frontend/raw/master/src/assets/images/banner03.png",
}
],
height: "1000px",
shareFlag:false,




index01: 0,
list2: list(),
demo2: '精选',
list3: ['收到的消息', '发出的消息'],
demo3: '收到的消息',
list4: ['正在放映', '即将上映'],
demo4: '即将上映',
demoDisabled: 'A',
index: 0,
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
}
}
},
created(){
this.$store.state.showTabbar = false
console.log(this.$store.state.showTabbar)
},
methods: {
/* // 分享课程的显示和隐藏
shareFlag(){
this.shareFlag = !shareFlag;
},*/


switchTabItem (index) {
console.log('on-before-index-change', index)
this.$vux.loading.show({
text: 'loading'
})
setTimeout(() => {
this.$vux.loading.hide()
this.index01 = index
}, 1000)
},
onItemClick (index) {
console.log('on item click:', index)
},
addTab () {
if (this.list2.length < 5) {
this.list2 = list().slice(0, this.list2.length + 1)
}
},
removeTab () {
if (this.list2.length > 1) {
this.list2 = list().slice(0, this.list2.length - 1)
}
},
next () {
if (this.index === this.list2.length - 1) {
this.index = 0
} else {
++this.index
}
},
prev () {
if (this.index === 0) {
this.index = this.list2.length - 1
} else {
--this.index
}
}
},
mounted(){












},
computed: {

}
}



